<template>
    <div class="container my-4">
        <div class="card my-4 border-0">
            <div class="card-header text-center bg-dark text-white">
                <strong>PANEL</strong>
            </div>
            <div class="my-2 mx-4">
                <table class="table table-sm text-center">
                    <thead>
                        <th colspan="2">Lista de Alertas</th>
                        <th colspan="2">Lista de Acciones</th>
                    </thead>
                    <tbody>
                        <tr>
                            <td>
                                <div 
                                    v-for="alert in buyAlertList" 
                                    v-bind:key="alert.instrument">
                                    <span class="badge badge-success"> {{ alert.instrument }}</span>
                                </div>
                            </td>
                            <td>
                                <div 
                                    v-for="alert in sellAlertList" 
                                    v-bind:key="alert.instrument">
                                    <span class="badge badge-danger"> {{ alert.instrument }}</span>
                                </div>
                            </td>
                            <td>
                                <div 
                                    v-for="action in buyActionList" 
                                    v-bind:key="action.instrument">
                                    <span class="badge badge-success"> {{ action.instrument }}</span>
                                </div>
                            </td>
                            <td>
                                <div 
                                    v-for="action in sellActionList" 
                                    v-bind:key="action.instrument">
                                    <span class="badge badge-danger"> {{ action.instrument }}</span>
                                </div>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
        <div class="accordion" id="accordionInstrument">
            <instrument-component 
                v-for="instrument in instruments" 
                v-bind:key="instrument.instrument" 
                v-bind:instrument="instrument"
                />
        </div>
    </div>    
</template>

<script>
import InstrumentComponent from './../components/InstrumentComponent'
import _ from 'lodash'
// import axios from 'axios'


export default {
    name: 'dashboard',
    components: {
        'instrument-component': InstrumentComponent,
    },
    data(){
        return {
            instruments: [],
        }
    },
    created(){
        // this.getCandles()
    },
    computed: {
        sellAlertList(){
            return _.filter(this.instruments, item => {
                 return (item.M5[0].signalA == 'SELL' && item.M15[0].signalA == 'SELL') 
            })
        }, 
        buyAlertList(){
            return _.filter(this.instruments, item => {
                 return (item.M5[0].signalA == 'BUY' && item.M15[0].signalA == 'BUY')
            })
        }, 
        sellActionList(){
            return _.filter(this.instruments, item => {
                 return item.M5[0].signalA == 'SELL' && item.M15[0].signalA == 'SELL' && item.S30[0].signalB == 'SELL'
            })
        },
        buyActionList(){
            return _.filter(this.instruments, item => {
                 return item.M5[0].signalA == 'BUY' && item.M15[0].signalA == 'BUY' && item.S30[0].signalB == 'BUY'
            })
        }
    },
    methods: {
        // getCandles(){
        //     axios.get('/instruments')
        //     .then(resp => {
        //         const data = _.groupBy(resp.data, item => item.instrument)
        //         this.instruments = _.map(data, item => {
        //             return {
        //                 instrument: item[0].instrument,
        //                 M15: _.filter(item, subItem => {
        //                     if(subItem.timeframe == 'M15') return subItem
        //                 }),
        //                 M5: _.filter(item, subItem => {
        //                     if(subItem.timeframe == 'M5') return subItem
        //                 }),
        //                 S30: _.filter(item, subItem => {
        //                     if(subItem.timeframe == 'S30') return subItem
        //                 }),
        //             }
        //         })
        //     }).catch(e => console.log(e))
        // }
    },
    sockets:{
        connect(){
            this.connected = true
        },
        disconnect(){
            this.connected = false
        },
        fxData(fxdata){
            // this.instruments = _.groupBy(data, item => item.instrument)
            const data = _.groupBy(fxdata, item => item.instrument)
            this.instruments = _.map(data, item => {
                return {
                    instrument: item[0].instrument,
                    M15: _.filter(item, subItem => {
                        if(subItem.timeframe == 'M15') return subItem
                    }),
                    M5: _.filter(item, subItem => {
                        if(subItem.timeframe == 'M5') return subItem
                    }),
                    S30: _.filter(item, subItem => {
                        if(subItem.timeframe == 'S30') return subItem
                    }),
                }
            })
        }
    }
}
</script>

