<template>
    <div>
        <div class="card text-center border-0">
            <div class="card-header bg-transparent border-0" v-bind:id="headingName" >
                <h5 class="mb-0">
                    <button 
                        class="btn btn-block collapsed" 
                        type="button" 
                        data-toggle="collapse" 
                        v-bind:data-target="cardCollapsableAnchor" 
                        aria-expanded="false" 
                        v-bind:aria-controls="cardCollapsableName"
                        v-bind:class="setAlert">
                        {{instrument.instrument}}: <span>{{ close }}</span>
                    </button>
                </h5>
            </div>

            <div 
                v-bind:id="cardCollapsableName" 
                class="collapse container mx-3 w-auto" 
                v-bind:aria-labelledby="headingName" 
                data-parent="#accordionInstrument">
                <!-- M15 Card -->
                <div class="card mb-2 border-0">
                    <div class="card-header border" v-bind:class="M15">M15 - {{ instrument.M15[0].time }}</div>
                    <div class="card-body pb-0">
                        <table class="table table-bordered table-sm">
                            <thead>
                                <tr>
                                    <th>Stoch(14)</th>
                                    <th>Stoch(6)</th>
                                    <th colspan="3">Bollinger(34)</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>{{ instrument.M15[0].indicatorStochLong }}</td>
                                    <td>{{ instrument.M15[0].indicatorStochShort }}</td>
                                    <td>{{ instrument.M15[0].indicatorLowerBand }}</td>
                                    <td>{{ instrument.M15[0].indicatorBolBand }}</td>
                                    <td>{{ instrument.M15[0].indicatorUpperBand }}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <!-- End M15 Card -->

                <!-- M5 Card -->
                <div class="card mb-2 border-0">
                    <div class="card-header border" v-bind:class="M5">M5 - {{ instrument.M5[0].time }}</div>
                    <div class="card-body pb-0">
                        <table class="table table-bordered table-sm">
                            <thead>
                                <tr>
                                    <th>Stoch(14)</th>
                                    <th>Stoch(6)</th>
                                    <th colspan="3">Bollinger(34)</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>{{ instrument.M5[0].indicatorStochLong }}</td>
                                    <td>{{ instrument.M5[0].indicatorStochShort }}</td>
                                    <td>{{ instrument.M5[0].indicatorLowerBand }}</td>
                                    <td>{{ instrument.M5[0].indicatorBolBand }}</td>
                                    <td>{{ instrument.M5[0].indicatorUpperBand }}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <!-- End M5 Card -->

                <!-- M15 Card -->
                <div class="card border-0">
                    <div class="card-header border" v-bind:class="S30">S30 - {{ instrument.S30[0].time }}</div>
                    <div class="card-body pb-0">
                        <table class="table table-bordered table-sm">
                            <thead>
                                <tr>
                                    <th>Wma(7)</th>
                                    <th>Ema(18)</th>
                                    <th>AO(4, 35)</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr>
                                    <td>{{ instrument.S30[0].indicatorWma }}</td>
                                    <td>{{ instrument.S30[0].indicatorEma }}</td>
                                    <td>{{ instrument.S30[0].indicatorAwesomeOsc }}</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
                <!-- End M15 Card -->
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "InstrumentComponent",
    props: {
        instrument: Object
    },
    data(){
        return {
            cardCollapsableName: 'cardCollasable'+this.instrument.instrument,
            cardCollapsableAnchor: '#cardCollasable'+this.instrument.instrument,
            headingName: 'heading-'+this.instrument.instrument,
        }
    }, 
    computed: {
        close(){
            return this.instrument.S30[0].close
        },
        open(){
            return this.instrument.S30[0].open
        },
        M15(){
            return {
                'bg-danger text-white': this.instrument.M15[0].signalA == 'SELL',
                'bg-success text-white': this.instrument.M15[0].signalA == 'BUY'
            }
        },
        M5(){
            return {
                'bg-danger text-white': this.instrument.M5[0].signalA == 'SELL',
                'bg-success text-white': this.instrument.M5[0].signalA == 'BUY'
            }
        },
        S30(){
            return {
                'bg-danger text-white': this.instrument.S30[0].signalB == 'SELL',
                'bg-success text-white': this.instrument.S30[0].signalB == 'BUY',
                
            }
        },
        setAlert(){
            return {
                'btn-danger': this.instrument.M15[0].signalA == 'SELL' && 
                              this.instrument.M5[0].signalA == 'SELL',
                'btn-success': this.instrument.M15[0].signalA == 'BUY' &&
                               this.instrument.M5[0].signalA == 'BUY'
            }
        }
    }
}
</script>

<style>

</style>
